<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">注册账号</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item prop="repeatPass">
      <el-input type="password" v-model="ruleForm2.repeatPass" auto-complete="off" placeholder="再次确认密码"></el-input>
    </el-form-item>
    
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">注册</el-button>
    </el-form-item>
    <el-form-item>
      <el-button style="width:100%;" @click.native.prevent="handleBackLogin">返回登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import { requestRegister } from '../api/api';
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: '',
          repeatPass:"123456"
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ],
          repeatPass:[
              {required:true,message:"请检查输入",trigger:'blur'}
          ]
        },
      };
    },
    methods: {
      handleBackLogin(){
        this.$router.push({path:'/login'});
      },
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      handleSubmit2(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            this.logining = true;
            var Params = { name: this.ruleForm2.account, pwd: this.ruleForm2.checkPass,
            repeatPwd:this.ruleForm2.repeatPass };
            requestRegister(Params).then(data => {
              this.logining = false;
              let { status,...datas } = data;
              console.log(data)
              console.log(status)
              if (status == "false") {
                this.$message({
                  message: datas.message,
                  type: 'error'
                });
              } else if(status=="success"){
                this.$message({
                  message: datas.message
                });
                //this.$router.push({path:"/login"})
              }else {
                this.$message({
                  message: datas.message
                });
              }
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


